import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Login',
        meta: {
            layout: 'white'
        },
        component: Login
    },
    {
        path: '/contacts',
        name: 'Contacts',
        meta: {
            layout: 'white'
        },
        component: () =>
            import ('../views/Contacts')
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router